.mf-real {
  flex: 1 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.mf-real .mf-real__desc {
  text-align: center;
  height: 100px;
  display: flex;
  align-items: center;
  font-size: 17px;
  color: #333333;
  letter-spacing: 0.17px;
  line-height: 24px;
  margin-bottom: 70px;
}
.mf-real .mf-real__qrcode img {
  width: 84px;
  height: 84px;
}
.mf-real .mf-real__qrcode__desc {
  font-size: 14px;
  color: #333333;
  letter-spacing: 0.14px;
  text-align: center;
  line-height: 24px;
  margin-top: 14px;
}
